// Navigation Bar (    ,      ( )  )
//         

import React, { useState } from 'react';
import LeftMenu from './Sections/LeftMenu';
import RightMenu from './Sections/RightMenu';
import { Drawer, Button } from 'antd';
import Icon, { SmileOutlined } from '@ant-design/icons';
import './Sections/Navbar.css';

function NavBar() {
  //const [visible, setVisible] = useState(false)
  // visible   open   
  const [open, setOpen] = useState(false);

  const showDrawer = () => {
      setOpen(true);
  };

  // const showDrawer = () => {
  //   setVisible(true)
  // };

  const onClose = () => {
    setOpen(false)
  };

  return (
    // menu__logo, menu__container  Sections/Navbar.css  
    <nav className="menu" style={{ paddingLeft: '150px', position: 'fixed', zIndex: 5, width: '100%' }}>
      <div className="menu__logo">
        <a href="/">
          <div style={{color : 'black', fontWeight: 'bold'}}>P
          <span style={{color : 'dodgerblue'}}>!</span>
          ck Me</div>
        </a>
      </div>
      <div className="menu__container">
        <div className="menu_left">
          {/* LeftMenu, RightMenu  Sections/     */}
          <LeftMenu mode="horizontal" />
        </div>
        <div className="menu_rigth">
          <RightMenu mode="horizontal" />
        </div>
        <Button
          className="menu__mobile-button"
          type="primary"
          onClick={showDrawer}
        >
          <Icon type="align-right" />
        </Button>
        <Drawer
          title="Basic Drawer"
          placement="right"
          className="menu_drawer"
          closable={false}
          onClose={onClose}
          open={open}
          //visible={visible}
        >
          <LeftMenu mode="inline" />
          <RightMenu mode="inline" />
        </Drawer>
      </div>
    </nav>
  )
}

export default NavBar