import React from "react";
import { Menu } from "antd";
import { useNavigate } from "react-router-dom";

//         ,       
function RightMenu(props) {
    const navigate = useNavigate();
    
    return (
        <Menu mode={props.mode}>
            <Menu.Item key="mail">
                <a href="/login"></a>
            </Menu.Item>
            <Menu.Item key="app">
                <a href="/register"></a>
            </Menu.Item>
        </Menu>
    );
}

export default RightMenu;